import React, { FC } from 'react';
import { Switch, Route, withRouter, RouteComponentProps } from 'react-router-dom';
import JournalsList from './JournalsList';
import CategoriesList from './CategoriesList';
import { myRoutes } from 'helper/routes';
import { Swipeable } from 'react-swipeable';

interface IProps extends RouteComponentProps {}

const Manage: FC<IProps> = ({ history }) => {
  const onSwipedLeft = () => {
    history.push(myRoutes.staticRoute.root);
  };
  return (
    <Swipeable delta={30} onSwipedLeft={onSwipedLeft} className='minVh'>
      <div className='manage_screen pt50'>
        <Switch>
          <Route path={myRoutes.staticRoute.manageCategory} component={CategoriesList} />
          <Route path={myRoutes.staticRoute.manage} exact component={JournalsList} />
        </Switch>
      </div>
    </Swipeable>
  );
};
export default withRouter(Manage);
