import React, { FC, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { myRoutes } from 'helper/routes';
import { OnIcon, OffIcon } from 'helper/icons';
import { IAplicationState } from 'store/reducers';
import { ISettingAction, ISettings, IUserParams } from 'helper/types';
import { connect } from 'react-redux';
import { params } from 'store/actions';

interface IStateProps {
  setting: ISettings;
  userParams: IUserParams;
}
interface IDispatchProps {
  changeSetting: (data: ISettingAction) => void;
  getParams: () => void;
}
interface IProps extends IStateProps, IDispatchProps {
  open: boolean;
  onClose: () => void;
}
export const Menu: FC<IProps> = ({ changeSetting, setting, open, onClose, userParams, getParams }) => {
  useEffect(() => {
    getParams();
  }, []);
  return (
    <div className={`menu ${open ? 'open' : ' '}`}>
      <div className='menu_bg' onClick={onClose} />
      <div className='menu_profile'>
        <div className='flex_between'>
          <img alt='images' src='https://picsum.photos/100/100' className='menu_profile_avatar' />
          <div className='menu_profile_info'>
            <div className='flex_between'>
              <div className='menu_profile_info_cat'>You read</div>
              <div className='menu_profile_info_num'>{userParams.user_statistics.you_read}</div>
            </div>
            <div className='flex_between'>
              <div className='menu_profile_info_cat'>bookmarks</div>
              <div className='menu_profile_info_num'>{userParams.user_statistics.bookmarks}</div>
            </div>
          </div>
        </div>
        <div className='menu_profile_nick'>User Name</div>
      </div>
      <div className='settings'>
        <Link to={myRoutes.staticRoute.manage} className='settings_journal'>
          Manage journals
        </Link>
        <div className='settings_subscrip'>Buy full subscription</div>
        <div className='swicher' onClick={() => changeSetting({ type: 'TOOGLE_Papers' })}>
          <div className='swicher_text'>Random papers</div>
          {setting.randomPapers ? <OnIcon /> : <OffIcon />}
        </div>
        <div className='swicher' onClick={() => changeSetting({ type: 'TOOGLE_MODE' })}>
          <div className='swicher_text'>Dark mode</div>
          {setting.darkMode ? <OnIcon /> : <OffIcon />}
        </div>
        <div className='swicher' onClick={() => changeSetting({ type: 'TOOGLE_Image' })}>
          <div className='swicher_text'>Disable image loading</div>
          {setting.disableImageLoading ? <OnIcon /> : <OffIcon />}
        </div>
        <div className='swicher' onClick={() => changeSetting({ type: 'TOOGLE_Factory' })}>
          <div className='swicher_text'>sort by impact factor</div>
          {setting.sortByImpactFactor ? <OnIcon /> : <OffIcon />}
        </div>
        <div className='swicher' onClick={() => changeSetting({ type: 'TOOGLE_View' })}>
          <div className='swicher_text'>journal based view</div>
          {setting.journalBasedView ? <OnIcon /> : <OffIcon />}
        </div>
      </div>
    </div>
  );
};
const mapStateToProps = ({ setting, userParams }: IAplicationState): IStateProps => ({
  setting,
  userParams,
});
const mapDispatchToProps = (dispatch: any): IDispatchProps => {
  return {
    getParams: () => params(dispatch),
    changeSetting: (data) => dispatch(data),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Menu);
