import axios from 'axios';

export const getSubCategory = (id: number | string) => async (dispatch: any) => {
  dispatch({ type: 'RESSET_SUBCATEGORY' });
  axios.get(`http://103.125.217.63:8700/subcategory?category_id=${id}`).then((res: any) => {
    // console.log(res.data, '');
    dispatch({ type: 'GET_SUBCATEGORY', payload: res.data });
  });
};
