import React, { FC } from 'react';
import { Switch, Route } from 'react-router-dom';
import { Article, ArticleListPage, BookMark, Category, Main, Manage, Reset, SingIn, SingUp, SubCategory, Login } from 'view';
import { myRoutes } from 'helper/routes';

export const Routers: FC = () => {
  return (
    <div className='layout'>
      <Switch>
        <Route exact path={myRoutes.staticRoute.root} component={Main} />
        <Route path={myRoutes.staticRoute.article} component={Article} />
        <Route path={myRoutes.staticRoute.reset} component={Reset} />
        <Route path={myRoutes.staticRoute.singIn} component={SingIn} />
        <Route path={myRoutes.staticRoute.singUp} component={SingUp} />
        <Route path={myRoutes.staticRoute.subCategory} component={SubCategory} />
        <Route path={myRoutes.staticRoute.category} component={Category} />
        <Route path={myRoutes.staticRoute.acticleList} component={ArticleListPage} />
        <Route path={myRoutes.staticRoute.manage} component={Manage} />
        <Route path={myRoutes.staticRoute.boormark} component={BookMark} />
        <Route path={myRoutes.staticRoute.login} component={Login} />
      </Switch>
    </div>
  );
};
