import { ISubCategory } from 'helper/types';

const initialState: ISubCategory[] = [];

type IAction = {
  type: 'GET_SUBCATEGORY' | 'RESSET_SUBCATEGORY';
  payload: ISubCategory[];
};
export function subCategoryReducer(state = initialState, action: IAction) {
  switch (action.type) {
    case 'GET_SUBCATEGORY':
      return action.payload;
    case 'RESSET_SUBCATEGORY':
      return [];
    default: {
      return state;
    }
  }
}
