import axios from 'axios';
import { R } from 'helper/R';

export const getJournalsSucategory = (id: string | number) => {
  return (dispatch: any) => {
    axios
      .get(`${R.api.journal()}?user_id=1&subcategory_id=${id}`)
      .then((res: any) => dispatch({ type: 'GET_SUBCATEGORY_JOURNAL', payload: res.data }))
      .catch((e) => console.log(e));
  };
};
