import { ISubJournals } from 'helper/types';

const initialState: ISubJournals[] = [];

type IAction = {
  type: 'GET_SUBCATEGORY_JOURNAL';
  payload: ISubJournals[];
};
export function subJournalsReducer(state = initialState, action: IAction) {
  switch (action.type) {
    case 'GET_SUBCATEGORY_JOURNAL':
      return action.payload;
    default: {
      return state;
    }
  }
}
