import React, { Fragment } from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { Swipeable } from 'react-swipeable';
import { myRoutes } from 'helper/routes';
import { IAplicationState } from 'store/reducers';
import { IMyJournal, ISettings, IArticleListItem } from 'helper/types';
import { getMyJournals, getRandomfeed } from 'store/actions';
import { connect } from 'react-redux';
import Menu from './Menu';
import { JournalsMainList, ActicleCard } from 'components';
interface IStateProps {
  myJournal: IMyJournal[];
  setting: ISettings;
  feeds: IArticleListItem[];
}
interface IDispatchProps {
  getMyJournals: (page: string | number) => void;
  getRandomfeed: (page: string | number) => void;
}
interface IProps extends IStateProps, IDispatchProps, RouteComponentProps {}

interface IState {
  openMenu: boolean;
  page: number;
  articlesPages: number;
}
class Main extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      page: 1,
      openMenu: false,
      articlesPages: 1,
    };
  }
  myRef: HTMLDivElement | null = null;
  handleScroll = () => {
    if (this.myRef) {
      let { bottom, top, height } = this.myRef.getBoundingClientRect();
      if (top + bottom + 100 > height) {
        let { myJournal } = this.props;
        let { page } = this.state;
        if (page * 15 <= myJournal.length) {
          this.setState({ page: page + 1 });
          this.props.getMyJournals(page + 1);
        }
      }
    }
  };
  handleScrollPages = (e: React.UIEvent<HTMLDivElement>) => {
    if (this.myRef) {
      let { bottom, top, height } = this.myRef.getBoundingClientRect();
      if (top * -1 + bottom + 100 > height) {
        let { feeds } = this.props;
        let { articlesPages } = this.state;
        if (articlesPages * 15 <= feeds.length) {
          console.log('this.myRef.getBoundingClientRect()');

          this.setState({ articlesPages: articlesPages + 1 });
          this.props.getRandomfeed(articlesPages + 1);
        }
      }
    }
  };
  componentDidMount() {
    if (this.props.myJournal.length === 0) {
      this.props.getMyJournals(1);
    }
    if (this.props.feeds.length === 0) {
      this.props.getRandomfeed(1);
    }
  }

  onSwipedRight = () => {
    this.setState({ openMenu: true });
  };
  onSwipedLeft = () => {
    if (this.state.openMenu) {
      this.setState({ openMenu: false });
    } else {
      this.props.history.push(myRoutes.staticRoute.boormark);
    }
  };
  render() {
    const { myJournal, setting, feeds, history } = this.props;
    const { openMenu } = this.state;
    return (
      <Swipeable delta={30} onSwipedRight={this.onSwipedRight} onSwipedLeft={this.onSwipedLeft} className='minVh'>
        {setting.journalBasedView ? (
          <div className='main_screen' onScroll={this.handleScrollPages}>
            <div ref={(ref) => (this.myRef = ref)}>
              {feeds.map((item, index) => {
                return (
                  <Fragment key={index}>
                    {index > 0 && index % 3 === 0 && <div className='ad_container'>New mouse assesmnet tool for locomtotr activity, online app</div>}
                    <ActicleCard {...item} />
                  </Fragment>
                );
              })}
            </div>
          </div>
        ) : (
          <div className='main_screen' onScroll={this.handleScroll}>
            <div className='manage_screen_content' ref={(ref) => (this.myRef = ref)}>
              <JournalsMainList myJournal={myJournal} toAdd={() => history.push(myRoutes.staticRoute.manageCategory)} />
            </div>
          </div>
        )}
        <Menu open={openMenu} onClose={this.onSwipedLeft} />
      </Swipeable>
    );
  }
}

const mapStateToProps = ({ myJournal, setting, feeds }: IAplicationState): IStateProps => ({
  myJournal,
  setting,
  feeds,
});
const mapDispatchToProps = (dispatch: any): IDispatchProps => {
  return {
    getMyJournals: (page: number | string) => getMyJournals(page)(dispatch),
    getRandomfeed: (page: string | number) => getRandomfeed(page)(dispatch),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));
