import axios, { AxiosResponse } from 'axios';
import { IMyJournal } from 'helper/types';
import { R } from 'helper/R';

export const getMyJournals = (page: string | number) => async (dispatch: any) => {
  axios
    .get(`${R.api.get_user_journals()}?user_id=1&page=${page}`)
    .then((res: AxiosResponse<IMyJournal[]>) => {
      // console.log(res.data);
      dispatch({ type: page == 1 ? 'GET_MY_JOURNAL' : 'ADD_MY_JOURNAL', payload: res.data });
    })
    .catch((e) => console.log(e));
};
