import React, { FC, useEffect } from 'react';
import { RightIcon, LeftIcon } from 'helper/icons';
import { Link, withRouter, RouteComponentProps } from 'react-router-dom';
import { IAplicationState } from 'store/reducers';
import { ICategory, ISubCategory } from 'helper/types';
import { getCategory } from 'store/actions';
import { connect } from 'react-redux';
import { getSubCategory } from 'store/actions/subCategory';
import { myRoutes } from 'helper/routes';

interface IStateProps {
  category: ICategory[];
  subCategory: ISubCategory[];
}
interface IDispatchProps {
  getCategory: () => void;
  getSubCategory: (id: string) => void;
}
interface IProps extends IStateProps, IDispatchProps, RouteComponentProps<{ id: string }> {}

const Category: FC<IProps> = ({ children, category, getCategory, match, getSubCategory, subCategory }) => {
  useEffect(() => {
    getCategory();
    getSubCategory(match.params.id);
  }, [match.params.id]);
  let mainCategory = category.find((item) => item.id === +match.params.id);

  return (
    <div className=''>
      <div className='categories pt64'>
        <Link className='categories_item main_category' to={myRoutes.staticRoute.manageCategory}>
          <LeftIcon />
          <div className='categories_item_title'>{mainCategory ? mainCategory.category_name : ''}</div>
        </Link>
        {subCategory.map((item, index) => (
          <Link to={myRoutes.goTo.goSubCategory(match.params.id, item.id)} className='categories_item' key={index}>
            <div className='categories_item_title'>{item.subcategory_name}</div>
            <RightIcon />
          </Link>
        ))}
      </div>
    </div>
  );
};

const mapStateToProps = ({ category, subCategory }: IAplicationState): IStateProps => ({
  category,
  subCategory,
});
const mapDispatchToProps = (dispatch: any): IDispatchProps => {
  return {
    getCategory: () => getCategory(dispatch),
    getSubCategory: (id: string) => dispatch(getSubCategory(id)),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Category));
