import React, { FC } from 'react';
import { BookMark_read } from 'helper/icons';
import { IArticleListItem } from 'helper/types';
import { Link } from 'react-router-dom';
import { myRoutes } from 'helper/routes';
import axios from 'axios';

const changeAutor = (autor: string): string => {
  let arrSrting = autor.split(' ');
  return `${arrSrting[1]} ${arrSrting[1]} ... ${arrSrting[arrSrting.length - 2]} ${arrSrting[arrSrting.length - 1]}`;
};
interface IProps extends IArticleListItem {
  onRefresh?: () => void;
}
export const ActicleCard: FC<IProps> = ({ onRefresh, authors, feed_id, feed_name, journal_name, pub_date, is_bookmark, is_read }) => {
  const addBookmark = (id: number) => {
    axios
      .get(`http://103.125.217.63:8800/add_bookmark?user_id=1&feed_id=${id}`)
      .then((res) => {
      })
      .catch((e) => {
      });
  };

  return (
    <div className={`acticle_card ${is_read ? 'read' : ''}`}>
      <div className="addictional_wrapper">
      <Link to={myRoutes.goTo.toArticle(feed_id)} className='acticle_card_title'>
        {journal_name}
      </Link>
      <Link to={myRoutes.goTo.toArticle(feed_id)} className='acticle_card_journal_title'>
        {feed_name}
      </Link>
      <div className='acticle_card_journal_container'>
        <div className='acticle_card_journal'>
          <div className='acticle_card_journal_desc'>{changeAutor(authors)}</div>
          <div className='acticle_card_journal_time'>{pub_date}</div>
        </div>
      </div>
      </div>
            {is_bookmark && <BookMark_read className='acticle_card_bookmark_icon' onClick={() => addBookmark(feed_id)} />}

    </div>
  );
};
