import React, { FC, useEffect, Fragment } from 'react';
import { BookMarkIcon } from 'helper/icons';
import { Swipeable } from 'react-swipeable';
import { Link, withRouter, RouteComponentProps } from 'react-router-dom';
import { myRoutes } from 'helper/routes';
import { IAplicationState } from 'store/reducers';
import { getBoormark } from 'store/actions';
import { connect } from 'react-redux';
import { IBookMarks } from 'helper/types';
import axios from 'axios';
import { Dispatch } from 'redux';
import Trash from '../../assets/svg/trash.svg'
interface IStateProps {
  boormark: IBookMarks[];
}
interface IDispatchProps {
  getBoormark: () => void;
  removeBookmark: (boormark: IBookMarks[]) => void;
}
interface IProps extends IStateProps, IDispatchProps, RouteComponentProps {}
interface IBookMarkItem extends IBookMarks {
  onClick: (id: number) => void;
}
const BookMarkItem: FC<IBookMarkItem> = ({ feed_id, feed_name, journal_name, onClick }) => {
  return (
    <div className='animate__animated animate__fadeInLeft book_card'>
      <Link to={myRoutes.goTo.toArticle(feed_id)} className='book_journal'>
        {journal_name}
      </Link>
      <div className='flex_between'>
        <Link to={myRoutes.goTo.toArticle(feed_id)} className='book_title'>
          {feed_name}
        </Link>
        <BookMarkIcon className='bookmark_icon' onClick={() => onClick(feed_id)} />
      </div>
    </div>
  );
};

const BookMark: FC<IProps> = ({ boormark, getBoormark, history, removeBookmark }) => {
  useEffect(() => {
    getBoormark();
  }, []);
  const onSwipedRight = () => {
    history.push(myRoutes.staticRoute.root);
  };
  const remveBook = (id: number) => {
    let newBokmark = boormark.filter((item) => item.feed_id !== id);
    axios
      .get(`http://103.125.217.63:8800/remove_bookmark?user_id=1&feed_id=${id}`)
      .then((res) => {
        removeBookmark(newBokmark);
      })
      .catch((e) => {
        console.log(e);
      });
  };
  return (
    <Swipeable delta={30} onSwipedRight={onSwipedRight} className='minVh'>
    <div className="button_area">
      <div className="manage_btn">
      <img src={Trash} className="delete_img" alt="img"/>
          <div className="btn_text">Delete All</div> 
      </div>
      </div>
      {boormark.map((item, index) => (
        <Fragment>
          {index > 0 && index % 3 === 0 && <div className='ad_container'>New mouse assesmnet tool for locomtotr activity, online app</div>}
          <BookMarkItem key={index} {...item} onClick={remveBook} />
        </Fragment>
      ))}
    </Swipeable>
  );
};
const mapStateToProps = ({ boormark }: IAplicationState): IStateProps => ({
  boormark,
});
const mapDispatchToProps = (dispatch: Dispatch): IDispatchProps => {
  return {
    removeBookmark: (boormark: IBookMarks[]) => dispatch({ type: 'NEW_STATE_BOOKMARK', payload: boormark }),
    getBoormark: () => getBoormark(dispatch),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(BookMark));
