import React, { FC, useEffect } from 'react';
import { IArticleListItem } from 'helper/types';
import { Swipeable } from 'react-swipeable';
import { myRoutes } from 'helper/routes';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import { ActicleCard } from 'components';
import { IAplicationState } from 'store/reducers';
import { getBasedfeed } from 'store/actions';
import { connect } from 'react-redux';

interface IStateProps {
  feeds: IArticleListItem[];
}
interface IDispatchProps {
  getBasedfeed: (id: string) => void;
}
interface IArticleListPageProps extends RouteComponentProps<{ id: string }>, IDispatchProps, IStateProps {}

export const ArticleListPage: FC<IArticleListPageProps> = ({ match, history, feeds, getBasedfeed }) => {
  const onSwipedRight = () => {
    history.push(myRoutes.staticRoute.root);
  };

  useEffect(() => {
    let id = match.params.id;
    getBasedfeed(id);
  }, [match.params.id]);
  console.log(feeds);
  return (
    <Swipeable delta={30} onSwipedRight={onSwipedRight} className='acticle minVh'>
      {feeds ? (
        <div>
          {feeds.map((item, index) => (
            <ActicleCard key={index} {...item} onRefresh={() => getBasedfeed(match.params.id)} />
          ))}
        </div>
      ) : (
        <div>loading...</div>
      )}
    </Swipeable>
  );
};
const mapStateToProps = ({ feeds }: IAplicationState): IStateProps => ({
  feeds,
});
const mapDispatchToProps = (dispatch: any): IDispatchProps => {
  return {
    getBasedfeed: (id: string) => getBasedfeed(id)(dispatch),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ArticleListPage));
