import React, { FC, Fragment } from 'react';
import { IMyJournal } from 'helper/types';
import { R } from 'helper/R';
import { Link } from 'react-router-dom';
import { myRoutes } from 'helper/routes';
import { AddIcon } from 'helper/icons';
import rightArrow from '../../assets/svg/right_arrow.svg' ;
import leftArrow from '../../assets/svg/left_arrow.svg' ;



interface IProps {
  myJournal: IMyJournal[];
  onClick?: (id: number) => void;
  toAdd: () => void;
}
export const JournalsMainList: FC<IProps> = ({ myJournal, toAdd }) => (
  <Fragment>
    {myJournal.length > 0 ? (
      myJournal.map((item, index) => {
        return (
          <Link to={myRoutes.goTo.toActicleList(item.journal_id)} key={index} className='journal_item'>
            <div className='counts'>{item.unread_feeds}</div>
            <img alt='images' src={R.imageHost + '/' + item.thumbnail} />
            <div className='journal_item_title'> {item.journal_name}</div>
          </Link>
        );
      })
    ) : (
      <div className='flexbox'>
        <div className='manage_btn' onClick={toAdd}>
          <AddIcon />
          <div className='manage_btn_text'>ADD NEW JOURNALS</div>
        </div>
        <div className="tutorial_arrow">
        <img src={rightArrow} alt="arrow_left"/>
			<span className="arrow_text">
				Right swipe - Bookmarks
			</span>
			<hr/>
      <img src={leftArrow} alt="arrow_left"/>
			<span className="arrow_text">
				Left swipe - Settings
			</span>
        </div>
      </div>
    )}
  </Fragment>
);
