import { IBookMarks } from 'helper/types';

const initialState: IBookMarks[] = [];

type IAction = {
  type: 'GET_BOOKMARK' | 'NEW_STATE_BOOKMARK';
  payload: IBookMarks[];
};
export function boormarkReducer(state = initialState, action: IAction): IBookMarks[] {
  switch (action.type) {
    case 'NEW_STATE_BOOKMARK':
      return action.payload;
    case 'GET_BOOKMARK':
      return action.payload;
    default: {
      return state;
    }
  }
}
