import React, { FC } from 'react';
import { Link } from 'react-router-dom';
import { myRoutes } from 'helper/routes';

interface IProps {
  onChangeText: (text: string) => void;
  value: string;
  placeholder: string;
  forgot?: true;
}

export const TextField: FC<IProps> = ({ onChangeText, value, placeholder, forgot }) => {
  return (
    <div className='input_container'>
      <input value={value} onChange={(e) => onChangeText(e.target.value)} placeholder={placeholder} className='regular_input' />
      {forgot && (
        <Link to={myRoutes.staticRoute.reset} className='link_auth'>
          Forgot password?
        </Link>
      )}
    </div>
  );
};
