import { ICategory } from 'helper/types';

const initialState: ICategory[] = [];

type IAction = {
  type: 'GET_CATEGORY';
  payload: ICategory[];
};
export function categoryReducer(state = initialState, action: IAction) {
  switch (action.type) {
    case 'GET_CATEGORY':
      return action.payload;
    default: {
      return state;
    }
  }
}
