import axios from 'axios';

export const getBasedfeed = (id: string | number) => {
  return (dispatch: any) => {
    dispatch({ type: 'GET_FEEDS', payload: [] });
    axios.get(`http://103.125.217.63:8900/get_journal_feeds?journal_id=${id}&page=1&user_id=1`).then((res: any) => {
      dispatch({ type: 'GET_FEEDS', payload: res.data });
    });
  };
};
export const getRandomfeed = (page: string | number) => async (dispatch: any) => {
  axios.get(`http://103.125.217.63:8900/get_user_feeds?page=${page}&user_id=1`).then((res: any) => {
    // console.log(res.data, 'getRandomfeed');
    dispatch({ type: page == 1 ? 'GET_FEEDS' : 'ADDS_FEEDS', payload: res.data });
  });
};
