import React, { FC, useEffect, useState, Fragment } from 'react';
import { IAplicationState } from 'store/reducers';
import { IMyJournal } from 'helper/types';
import { getMyJournals } from 'store/actions';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import axios from 'axios';
import { R } from 'helper/R';
import { UnselectedIcon, SelectedIcon, AddIcon, LeftIcon } from 'helper/icons';
import { myRoutes } from 'helper/routes';

interface IStateProps {
  myJournal: IMyJournal[];
}
interface IDispatchProps {
  getMyJournals: () => void;
  removeJounals: (myJournal: IMyJournal[]) => void;
}
interface IProps extends IStateProps, IDispatchProps, RouteComponentProps {}
const JournalsList: FC<IProps> = ({ myJournal, getMyJournals, history, removeJounals }) => {
  let [removeArray, setRemoveArray] = useState<number[]>([]);
  useEffect(() => {
    getMyJournals();
  }, []);
  const addRemover = (id: number) => {
    if (removeArray.includes(id)) {
      setRemoveArray([...removeArray.filter((item) => item !== id)]);
    } else {
      setRemoveArray([...removeArray, id]);
    }
  };
  const removeJournals = () => {
    if (removeArray.length > 0) {
      let newMyJournal = myJournal.filter((item) => !removeArray.includes(item.journal_id));

      axios
        .get(`http://103.125.217.63:8800/remove_journal_id_list?user_id=1&journal_ids=[${removeArray}]`)
        .then((res) => {
          removeJounals(newMyJournal);
          setRemoveArray([]);
        })
        .catch((e) => {
          console.log(e);
        });
    }
  };

  return (
    <Fragment>
      <div className='manage_screen_tab'>
        <LeftIcon onClick={() => history.push(myRoutes.staticRoute.root)} />
        <div className='manage_btn' onClick={() => history.push(myRoutes.staticRoute.manageCategory)}>
          <AddIcon />
          <div className='manage_btn_text'>ADD NEW JOURNALS</div>
        </div>
        <div onClick={removeJournals} className='actions_remove'>
          Remove
        </div>
      </div>
      <div className='manage_screen_content'>
        {myJournal.map((item, index) => {
          let isSelect = removeArray.includes(item.journal_id);
          return (
            <div key={index} onClick={() => addRemover(item.journal_id)} className={`journal_item ${isSelect ? 'selected' : 'none'}`}>
              {removeArray.length > 0 && <div className='selected_container'>{isSelect ? <SelectedIcon /> : <UnselectedIcon />}</div>}
              <img alt='images' src={R.imageHost + '/' + item.thumbnail} />
              <div className='journal_item_title'> {item.journal_name}</div>
            </div>
          );
        })}
      </div>
    </Fragment>
  );
};
const mapStateToProps = ({ myJournal }: IAplicationState): IStateProps => ({
  myJournal,
});
const mapDispatchToProps = (dispatch: any): IDispatchProps => {
  return {
    getMyJournals: () => getMyJournals('1')(dispatch),
    removeJounals: (myJournal: IMyJournal[]) => dispatch({ type: 'NEW_STATE_JOURNALS', payload: myJournal }),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(JournalsList));
