import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import { Routers } from './Routers';
import { Provider } from 'react-redux';
import store from 'store';

function App() {
  return (
    <Provider store={store}>
      <Router>
        <div className='App'>
          <Routers />
        </div>
      </Router>
    </Provider>
  ); 
}

export default App;
