import { IUserParams } from 'helper/types';

const initialState: IUserParams = {
  first_name: '',
  second_name: '',
  token: '',
  user_id: 1,
  user_pic: '',
  user_statistics: {
    bookmarks: 0,
    you_read: 0,
  },
};

type IAction = {
  type: 'GET_PARAMS';
  payload: IUserParams[];
};
export function paramsRedusers(state = initialState, action: IAction) {
  switch (action.type) {
    case 'GET_PARAMS':
      return action.payload;
    default: {
      return state;
    }
  }
}
