import { combineReducers } from 'redux';
import { categoryReducer } from './category';
import { subJournalsReducer } from './subJournals';
import { subCategoryReducer } from './subCategory';
import { myJournalsReducer } from './myJournals';
import { boormarkReducer } from './boormark';
import { settingsReducer } from './setings';
import { basedfeedReducer } from './basedfeed';
import { paramsRedusers } from './params';
const appReducer = combineReducers({
  category: categoryReducer,
  subJournals: subJournalsReducer,
  subCategory: subCategoryReducer,
  myJournal: myJournalsReducer,
  boormark: boormarkReducer,
  setting: settingsReducer,
  feeds: basedfeedReducer,
  userParams: paramsRedusers,
});

const rootReducers = (state: any, action: any) => {
  if (action.type === 'DESTROY_SESSION') {
    state = {};
  }
  if (action.type === 'NEW_STATE') {
    return action.payload;
  }
  return appReducer(state, action);
};

export type IAplicationState = ReturnType<typeof appReducer>;

export { rootReducers };
