import axios, { AxiosResponse } from 'axios';
import { IUserParams } from 'helper/types';

export const params = async (dispatch: any) => {
  axios
    .get(`http://103.125.217.63:8800/get_user_params?user_email=k.valov@mail.ru`)
    .then((res: AxiosResponse<IUserParams[]>) => {
      console.log('GET_PARAMS');
      console.log(res.data);
      dispatch({ type: 'GET_PARAMS', payload: res.data });
    })
    .catch((e) => console.log(e));
};
