import React, { FC, useEffect, useState } from 'react';
import { RouteComponentProps, withRouter } from 'react-router-dom';
import { LeftIcon, UnselectedIcon, SelectedIcon } from 'helper/icons';
import { IAplicationState } from 'store/reducers';
import { connect } from 'react-redux';
import { ISubJournals, ISubCategory } from 'helper/types';
import { getJournalsSucategory } from 'store/actions/journals';
import axios from 'axios';
import { getSubCategory } from 'store/actions/subCategory';
import { R } from 'helper/R';

interface IStateProps {
  subJournals: ISubJournals[];
  subCategory: ISubCategory[];
}
interface IDispatchProps {
  getJournalsSucategory: (id: number | string) => void;
  getSubCategory: (id: string) => void;
}
interface IProps extends IStateProps, IDispatchProps, RouteComponentProps<{ id: string; category_id: string }> {}
const SubCategory: FC<IProps> = ({ match, subJournals, getJournalsSucategory, history, subCategory, getSubCategory }) => {
  let [addArray, setAddArray] = useState<number[]>([]);
  const addIndexes = (id: number) => {
    if (addArray.includes(id)) {
      setAddArray([...addArray.filter((item) => item !== id)]);
    } else {
      setAddArray([...addArray, id]);
    }
  };
  useEffect(() => {
    if (subCategory.length === 0) {
      getSubCategory(match.params.category_id);
    }
    getJournalsSucategory(match.params.id);
  }, [match.params.id]);
  const addJournals = () => {
    axios
      .get(`http://103.125.217.63:8800/add_journal_id_list?user_id=1&journal_ids=[${addArray}]`)
      .then((res) => {
        getJournalsSucategory(match.params.id);
        setAddArray([]);
      })
      .catch((e) => {
        console.log(e);
      });
  };
  const getSub = subCategory.find((item) => item.id === +match.params.id);

  return (
    <div>
      <div className=' categories_item main_category'>
        <LeftIcon onClick={() => history.goBack()} />
        <div className='categories_item_title'>{getSub ? getSub.subcategory_name : ''}</div>
        <div onClick={addJournals} className='actions_remove'>
          Follow
        </div>
      </div>
      <div className='mt64' />
      <div className='manage_screen_content '>
        {subJournals.map((item, index) => {
          let isSelect = addArray.includes(item.journal_id);
          return (
            <div
              key={index}
              className='journal_item'
              onClick={() => {
                if (item.is_followed === 0) {
                  addIndexes(item.journal_id);
                }
              }}
            >
              {addArray.length > 0 && item.is_followed === 0 && <div className='selected_container'>{isSelect ? <SelectedIcon /> : <UnselectedIcon />}</div>}
              <img alt='images' src={R.imageHost + '/' + item.thumbnail} />
              <div className='journal_item_title'>{item.journal_name}</div>
              <div
                className={`journal_item_remove ${item.is_followed === 1 ? 'follow' : ''}`}
                onClick={() => {
                  if (item.is_followed === 0) {
                    addIndexes(item.journal_id);
                  }
                }}
              >
                {item.is_followed === 0 ? 'follow' : 'you follow'}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

const mapStateToProps = ({ subJournals, subCategory }: IAplicationState): IStateProps => ({
  subJournals,
  subCategory,
});
const mapDispatchToProps = (dispatch: any): IDispatchProps => {
  return {
    getJournalsSucategory: (id) => {
      dispatch(getJournalsSucategory(id));
    },
    getSubCategory: (id: string) => dispatch(getSubCategory(id)),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(SubCategory));
