import React from 'react';
import Google from '../../assets/svg/google.svg';
import Twitter from '../../assets/svg/twitter.svg';
import Facebook from '../../assets/svg/facebook.svg';
import GooglePlay from '../../assets/svg/googlePlay.svg';
import AppStore from '../../assets/svg/appStore.svg';




export  const Login = () => {
  return (
    <div className='wrapper'>
        <h1 className="login__title">Find out</h1>
        <h3 className="login_description">what's happening <br/>
          in the world right now!
        </h3>
		<figure className='withLogin'>
			<img src={Google} className="auth_img"alt="auth_img"/>
			<figcaption>Login with google</figcaption>
		</figure>
		<figure className='withLogin'>
			<img src={ Facebook } className="auth_img"alt="auth_img"/>
			<figcaption>Login with facebook</figcaption>
		</figure>
		<figure className='withLogin'>
			<img src={Twitter} className="auth_img"alt="auth_img"/>
			<figcaption>Login with twitter</figcaption>
		</figure>
		<div className="markets">
			<img src={GooglePlay} className="markets "alt="mark1"/>
			 <img src={AppStore} className="markets "alt="mark1"/> 
		</div>
    </div>
  );
};