import { IMyJournal } from 'helper/types';

const initialState: IMyJournal[] = [];

type IAction = {
  type: 'GET_MY_JOURNAL' | 'ADD_MY_JOURNAL' | 'NEW_STATE_JOURNALS';
  payload: IMyJournal[];
};
export function myJournalsReducer(state = initialState, action: IAction) {
  switch (action.type) {
    case 'NEW_STATE_JOURNALS':
      return action.payload;
    case 'GET_MY_JOURNAL':
      return action.payload;
    case 'ADD_MY_JOURNAL':
      return [...state, ...action.payload];
    default: {
      return state;
    }
  }
}
