import { ISettingAction, ISettings } from 'helper/types';

const initialState: ISettings = {
  isOpenMenu: false,
  randomPapers: false,
  darkMode: false,
  disableImageLoading: false,
  sortByImpactFactor: false,
  journalBasedView: false,
};

export function settingsReducer(state = initialState, action: ISettingAction): ISettings {
  switch (action.type) {
    case 'TOOGLE_MENU':
      return { ...state, isOpenMenu: !state.isOpenMenu };
    case 'TOOGLE_Image':
      return { ...state, disableImageLoading: !state.disableImageLoading };
    case 'TOOGLE_Factory':
      return { ...state, sortByImpactFactor: !state.sortByImpactFactor };
    case 'TOOGLE_MODE':
      return { ...state, darkMode: !state.darkMode };
    case 'TOOGLE_Papers':
      return { ...state, randomPapers: !state.randomPapers };
    case 'TOOGLE_View':
      return { ...state, journalBasedView: !state.journalBasedView };
    default: {
      return state;
    }
  }
}
