import React, { FC, useEffect, Fragment } from 'react';
import { RightIcon, LeftIcon } from 'helper/icons';
import { Link, withRouter, RouteComponentProps } from 'react-router-dom';
import { IAplicationState } from 'store/reducers';
import { Dispatch } from 'redux';
import { ICategory } from 'helper/types';
import { getCategory } from 'store/actions';
import { connect } from 'react-redux';
import { myRoutes } from 'helper/routes';

interface IStateProps {
  category: ICategory[];
}
interface IDispatchProps {
  getCategory: () => void;
}
interface IProps extends IStateProps, IDispatchProps, RouteComponentProps {}
const CategoriesList: FC<IProps> = ({ category, getCategory, history }) => {
  useEffect(() => {
    getCategory();
  }, []);
  return (
    <Fragment>
      <div className='manage_screen_tab'>
        <LeftIcon onClick={() => history.push(myRoutes.staticRoute.manage)} />
        <div className={`tab_item mr12 active`}>ADD JOURNALS</div>
      </div>
      <div className='categories'>
        {category.map((item, index) => (
          <Link to={myRoutes.goTo.goCategory(item.id)} className='categories_item' key={index}>
            <div className='categories_item_title'>{item.category_name}</div>
            <RightIcon />
          </Link>
        ))}
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({ category }: IAplicationState): IStateProps => ({
  category,
});
const mapDispatchToProps = (dispatch: Dispatch): IDispatchProps => {
  return {
    getCategory: () => getCategory(dispatch),
  };
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(CategoriesList));
