import { ICategory, IArticleListItem } from 'helper/types';

const initialState: ICategory[] = [];

type IAction = {
  type: 'GET_FEEDS' | 'ADDS_FEEDS';
  payload: IArticleListItem[];
};
export function basedfeedReducer(state = initialState, action: IAction) {
  switch (action.type) {
    case 'GET_FEEDS':
      return action.payload;
    case 'ADDS_FEEDS':
      console.log('ADDS_FEEDS');
      console.log(action, [...state, ...action.payload]);
      return [...state, ...action.payload];
    default: {
      return state;
    }
  }
}
