import React, { FC, useEffect, useState } from 'react';
import { BookMarkIcon, SpinerIcon } from 'helper/icons';
import { Swipeable } from 'react-swipeable';
import { RouteComponentProps } from 'react-router-dom';
import axios, { AxiosResponse } from 'axios';
import { IArticle } from 'helper/types';
import { R } from 'helper/R';
import { myRoutes } from 'helper/routes';

interface IProps extends RouteComponentProps<{ article_id: string }> {}
export const Article: FC<IProps> = ({ match, history }) => {
  let [article, setArticle] = useState<IArticle | null>(null);
  useEffect(() => {
    axios
      .get(`http://103.125.217.63:8800/add_read_feed?user_id=1&feed_id=${match.params.article_id}`)
      .then((e: AxiosResponse<IArticle>) => {
        console.log(e.data, 'e.data');
      })
      .catch((e) => console.log(e));
    axios
      .get(`http://103.125.217.63:8900/get_feed?feed_id=${match.params.article_id}`)
      .then((e: AxiosResponse<IArticle>) => {
        console.log(e.data);
        setArticle(e.data);
      })
      .catch((e) => console.log(e));
  }, [match.params.article_id]);

  const addBookmark = () => {
    axios
      .get(`http://103.125.217.63:8800/add_bookmark?user_id=1&feed_id=${match.params.article_id}`)
      .then((res) => {
        // console.log(res.data);
      })
      .catch((e) => {
        console.log(e);
      });
  };
  const onSwipedRight = () => {
    history.push(myRoutes.staticRoute.root);
  };
  return article ? (
    <Swipeable delta={30} onSwipedRight={onSwipedRight} className='acticle'>
      <div className='acticle_title_container'>
        <div className='acticle_title'>{article.feed_name}</div>
        <BookMarkIcon className='acticle_icon' onClick={addBookmark} />
      </div>

      <div className='acticle_media'>
        <img src={R.imageHost + '/' + article.ga_path} alt='images' />
      </div>
      <div className='acticle_content'>{article.abstract}</div>
    <button className="button">copy doi</button>
    <button className="button">full text</button>


</Swipeable>
  ) : (
    <div className='center_loader'>{<SpinerIcon className='svg_animated' />}</div>
  );
};
